import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
  state:{
    // 
    detail:false,//header
    shoppingCarDetail:false,//
    foodInfo:false,//food
    resume:false,//
    sellerData:{},//appseller
    goodsData:[],//appgoods
    ratingDataList:[],//apprating
    selectedFoodList:[],//
    picture:false,//
  },
  mutations:{
    // 
    // header
    showDetail:(state,payload) => {
      state.detail = true;
    },
    // header
    hideDetail:(state,payload) => {
      state.detail = false;
    },
    // 
    showShoppingCarDetail:(state,payload) => {
      state.shoppingCarDetail = true;
    },
    // 
    hideShoppingCarDetail:(state,payload) => {
      state.shoppingCarDetail = false;
    },
    // food
    showFoodInfo:(state) => {
      state.foodInfo = true;
    },
    // food
    hideFoodInfo:(state) => {
      state.foodInfo = false;
    },
    // resume
    showResume:(state) => {
      state.resume = true;
    },
    // resume
    hideResume:(state) => {
      state.resume = false;
    },
    // 
    showPicture:(state) => {
      state.picture = true;
    },
    // 
    hidePicture:(state) => {
      state.picture = false;
    },
  },
  actions:{
    // 
    // header
    showDetail:({commit,state},product) => {
      commit('showDetail')
    },
    // header
    hideDetail:({commit,state},product) => {
      commit('hideDetail')
    },
    // 
    showShoppingCarDetail:({commit,state},product) => {
      commit('showShoppingCarDetail')
    },
    // 
    hideShoppingCarDetail:({commit,state},product) => {
      commit('hideShoppingCarDetail')
    },
    // food
    showFoodInfo:({commit}) => {
      commit('showFoodInfo');
    },
    // food
    hideFoodInfo:({commit}) => {
      commit('hideFoodInfo');
    },
    // resume
    showResume:({commit}) => {
      commit('showResume');
    },
    // resume
    hideResume:({commit}) => {
      commit('hideResume')
    },
    // 
    showPicture:({commit}) => {
      commit('showPicture')
    },
    // 
    hidePicture:({commit}) => {
      commit('hidePicture')
    }
  },
  getters:{
    // 
    //header
    detail:(state) => {
      return state.detail;
    },
    //
    shoppingCarDetail:(state) => {
      return state.shoppingCarDetail;
    },
    // food
    foodInfo:(state) => {
      return state.foodInfo;
    },
    //
    resume:(state) => {
      return state.resume;
    },
    // 
    picture:(state) => {
      return state.picture;
    }
  }
});

export default store



