<template>
    <div id="resumeView">
      <!---->
      <div class="name"></div>
      <!---->
      <div class="avatar-wrapper">
        <div class="avatar"><img src="./FeXXX.jpg"></div>
      </div>
      <!---->
      <div class="contact">
        <div class="tel">
          <span class="glyphicon glyphicon-earphone"></span>
          13427540580
        </div>
        <div class="email">
          <span class="glyphicon glyphicon-envelope"></span>
          377162713@qq.com
        </div>
      </div>
      <!---->
      <div class="introduce">
        <h2 class="title">My-skill</h2>
        <p class="content">
          1HTML5CSS3,div+css;<br>
          2firework;<br>
          3javascriptES6;<br>
          4ajax,jsonp;<br>
          5jQueryVuevuex,vue-router<br>
          6,webpack;<br>
          7w3c<br>
          8bug
        </p>
      </div>
      <!---->
      <button class="close-btn btn btn-danger" @click="closeResume()">({{count}})s</button>
    </div>
</template>

<script>
    export default {
      name: "resume-view",
      data(){
        return {
          count:0,
        }
      },
      watch:{
        // count,0closeResume
        count(){
          if(this.count == 0){
            this.closeResume();
          }
        },
      },
      methods:{
        // resume
        closeResume(){
          this.$store.dispatch('hideResume')
        },
        // 
        countDown(sec){
          this.count = sec;
          setInterval(() => {
            this.count --;
          },1000)
        }

      },
      mounted(){
        // 
        this.countDown(5);
      }
    }
</script>

<style scoped lang="less">
  @r:750/10rem;
  #resumeView {
    background: linear-gradient(#F0AB70 , #cccccc);
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1000;
    .close-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      width: 200/@r;
      height: 50/@r;
      right: 20/@r;
      top: 20/@r;
      font-size: 20/@r;
      font-weight: 500;
      color: rgb(0,0,0);
    }
    .name {
      /*background-color:pink;*/
      margin-top: 100/@r;
      line-height: 100/@r;
      font-size: 100/@r;
      font-weight: 700;
      text-align: center;
      text-shadow: 0 0 20px rgba(0,0,0,0.7);
      animation: name 2s infinite;
    }
    .avatar-wrapper {
      perspective: 800;
      margin-top: 20/@r;
      .avatar {
        width: 200/@r;
        height: 200/@r;
        margin: 0 auto;
        border-radius: 50%;
        overflow: hidden;
        animation: avatar 2s infinite;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
    .contact {
      .tel {
        /*background-color: violet;*/
        margin-top: 50/@r;
        font-size: 50/@r;
        font-weight: 700;
        text-align: center;
        text-decoration: underline;
      }
      .email {
        font-size: 50/@r;
        font-weight: 700;
        text-align: center;
      }
    }
    .introduce {
      margin-top: 20/@r;
      padding: 0 20/@r 0 20/@r;
      .title {
        font-size: 40/@r;
        font-weight: 700;
        line-height: 60/@r;
      }
      .content {
        line-height: 36/@r;
        font-size: 32/@r;
        color: darkblue;
      }
    }

    /**/
    @keyframes avatar {
      0% {transform:translateZ(0) rotateZ(-30deg);}
      50% {transform:translateZ(200/@r) rotateZ(30deg)}
      100% {transform:translateZ(0) rotateZ(-30deg);}
    }
    /**/
    @keyframes name {
      0% {transform:scale(1)}
      50% {transform:scale(1.2)}
      100% {transform:scale(1)}
    }
  }
</style>



